import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function capexToOperatingCashFlowTtm(tableName: string) {
  const title = "Capex/Op Cash Flow (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["capexToOperatingCashFlowTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Capex spending relative to operating cash flow.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about capex to operating cash flow or other relevant financial measures?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data
                  ?.capexToOperatingCashFlowTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.capexToOperatingCashFlowTtm ??
              0) * 100
          }
        />
      )
    },
  })
}

export { capexToOperatingCashFlowTtm }
